import { ActivatedRoute, Router, Params } from '@angular/router';

export default class UtilsService {

    static stringEnumToKeyValue(stringEnum) {
        const keyValue = [];
        const keys = Object.keys(stringEnum).filter((value, index) => {
            return !(index % 2);
        });

        for (const k of keys) {
            keyValue.push({key: k, value: stringEnum[k]});
        }

        return keyValue;
    }

    static updateQueryParams(router: Router, route: ActivatedRoute, queryParams: Params) {
        router.navigate([], 
          {
            relativeTo: route,
            queryParams: queryParams, 
            queryParamsHandling: 'merge', // remove to replace all query params by provided
          });
    }

}